










































































import Vue from 'vue'

import axios from 'axios'
import VueAxios from 'vue-axios'

import Nav from '@/components/Nav.vue'
export default Vue.extend ({
  name: "EncuestasHtml",
    components: {
      Nav,
  },
  data() : any {
    return {
      datos: {},
      url: '',
      path:[] as any,
      formulario:null,
      text: '',
      alertVariant:'',
      respuesta:{
        valor_respuesta: null
      },
      idencuesta:null,
      reinicioEncuesta: [],
      errors:[],
      submitted: false,
      valor_respuesta: null,
      
      togglerAnswer: false,
      toggledAnswerIndex:{} as any,
      grupo_id: null,
    };
  },

  props: {
	encuesta_id:{
		type:Number,
    required: true
    }
  },
  methods:{

    async getEncuesta(){ 
      axios({
      method: "post",
      url:"http://192.168.60.105:3007/ugcc-encuestas/encuesta",
      data:{
          id: (this as any).encuesta_id
        }
      })
      .then( response => { this.datos = response.data.body;
      this.formulario = response.data.ok })
    },
    async postEncuesta(e : any): Promise<void> {
      await axios.post('http://192.168.60.105:3007/ugcc-encuestas/guardar-encuesta', this.datos )
      .then((res) => { 
        setTimeout( () => this.$router.push({ path : "/" }), 1000);
        /*  const url = window.location.href
        setTimeout( () => this.$router.go({ path : this.url}), 1500); */
        this.alertVariant = "success"
        this.text = "Envío Exitoso!"     
        console.warn(res)
      })
      .catch((error) => {
        this.alertVariant = "danger"
        this.text = "Error en el envío"
        console.warn(error)
      }) 
      e.preventDefault()
    },
    toggleAnswer (pregunta_id : any) {
      if(!this.togglerAnswer) {
        this.togglerAnswer = true;
        this.toggledAnswerIndex = pregunta_id;
      } else {
        this.togglerAnswer = false;
        this.toggledAnswerIndex = -1;
      }
    }
  },
  mounted() {
    this.getEncuesta()
  }
})
